import React from 'react'
import axios from 'axios'
import * as bs from 'react-bootstrap'
import AppContext from './context.js'
import { Formik, Form, Field} from 'formik'
import { Elements, CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { useHistory } from 'react-router-dom'

const stripePromise = loadStripe("pk_test_S21YezIw2QBn8SXH89kB0uQ200rYoIY56T")

function Covid_Prediction(props) {
    // we'll add Stripe's Elements component here later
    return ( 
        <Elements stripe={stripePromise}>
            <PredictionController />
        </Elements>
    )
}

export default Covid_Prediction


const PredictionController = props => {

    const state = React.useContext(AppContext)
    const stripe = useStripe()
    const elements = useElements()
    const total = state.getCartTotal()
    const history = useHistory()
    const [stripeError, setStripeError] = React.useState(null)
    return (
        <Formik
            initialValues={{
                name: 'Matthew Tannnyhill',
                address1: '1234',
                address2: '5678',
                city: 'Provo',
                state: 'UT',
                zipcode: '84604',
            }}
            validateOnChange={false}
            validateOnBlur={false}
            validate={values => {
                const errors = {}
                if(!values.name){
                    errors.name = "Required"
                }
                if(!values.address1){
                    errors.address1 = "Required"
                }
                if(!values.city){
                    errors.city = "Required"
                }
                if(!values.state){
                    errors.state = "Required"
                }
                if(!values.zipcode){
                    errors.zipcode = "Required"
                }
                return errors
            }}

            onSubmit={async (values, actions) => {

                let cart = []
            
                Object.keys(state.cart).forEach(function(key) {
                    
                    let product = null
            
                    for (const p of state.products) {
                        let num = '' + p.id
                        if (num === key) {
                            product = p
                        }
                    }
                        
                    if(product){

                        cart.push({
                            pid: product.id,
                            qty: state.cart[key],
                            price: parseFloat(product.price),
                        })
                    }
                })
                
                const payment_intent = await axios.post('/api/sale/', {
                    name: values.name,
                    address1: values.address1,
                    address2: values.address2,
                    city: values.city,
                    state: values.state,
                    zipcode: values.zipcode,
                    total: total.total,
                    items: cart,
                })

                const stripeResp = await stripe.confirmCardPayment(payment_intent.data.client_secret, {
                    payment_method: {
                        card: elements.getElement(CardElement),
                        billing_details: {
                            name: values.name,
                        },
                    }
                })

                if (stripeResp.error){
                    setStripeError(stripeResp.error.message)
                    return
                }
                else{
                    // go to the thank you page and clear out cart
                    state.clearCart()
                    history.push('/receipt')
                }
                
                await new Promise(resolve => {
                    setTimeout(() => {  // wait 2 seconds, then set the form as "not submitting"
                        resolve()
                    }, 3000)
                })
            }}
        >{form => (
            <div>
                {stripeError &&
                    <p className="danger">{stripeError}</p>
                } 

                <PaymentForm form={form} total={total} />
            </div>
        )}</Formik>
    )
}


/**
 * The form layout/html.
 * This component needs finishing.
 */
const PaymentForm = props => (
    <div>
        <bs.Container>
            &nbsp;
            <h2>Checkout</h2>
            &nbsp;
            <Form>
                <bs.Row>
                    <bs.Col>
                            <div className="p-3 mb-0 bg-info rounded-top border border-bottom-0 border-dark">
                                <h5>Shipping</h5>
                            </div>
                            <div className="p-3 mb-0 bg-white rounded-bottom border border-dark">
                                <Input title="Name:" name="name" type="text" disabled={props.form.isSubmitting} />
                                <Input title="Address 1:" name="address1" type="text" disabled={props.form.isSubmitting}/>
                                <Input title="Address 2:" name="address2" type="text" disabled={props.form.isSubmitting}/>
                                <Input title="City:" name="city" type="text" disabled={props.form.isSubmitting}/>
                                <Input title="State:" name="state" type="text" disabled={props.form.isSubmitting}/>
                                <Input title="Zip Code:" name="zipcode" type="text" disabled={props.form.isSubmitting}/>
                            </div>
                    </bs.Col>
                    <bs.Col>
                        <div className="p-3 mb-0 bg-info rounded-top border border-bottom-0 border-dark">
                            <h5>Payment</h5>
                        </div>
                        <div className="p-3 mb-0 bg-white rounded-bottom border border-dark">
                            <CardElement />
                        </div>
                        &nbsp;
                        <div>Your card will be charged {props.total.total}</div>
                        &nbsp;
                        <div>
                            <bs.Button
                                variant="secondary"
                                type="submit"
                                disabled={props.form.isSubmitting}
                            >
                                {props.form.isSubmitting && <bs.Spinner animation="border" variant="dark" size="sm" />}
                                {' '}{' '}{' '}
                                {props.form.isSubmitting && <span>Loading</span>}
                                {!props.form.isSubmitting && <span>Purchase</span>} 
                            </bs.Button>
                        </div>
                    </bs.Col>
                </bs.Row>
            </Form>

        </bs.Container>
    </div>
)

 
/**
 * A form input.
 *   props.title - the title that shows above the input box
 *   props.type - the type of input (see React Bootstrap Form.Control)
 *   props.placeholder - placeholder text in the input.
 * This component is finished and doesn't need additional work.
 */
const Input = (props) => (
    <Field name={props.name}>{rProps => (
        <bs.Form.Group>
            {props.title &&
                <bs.Form.Label>{props.title}</bs.Form.Label>
            }
            <bs.Form.Control
                type={props.type}
                placeholder={props.placeholder}
                disabled={props.disabled}
                {...rProps.field}
            />
            {rProps.meta.touched && rProps.meta.error &&
                <div className="text-danger">{rProps.meta.error}</div>
            }
        </bs.Form.Group>
    )}</Field>
)
