import React from 'react'
import * as bs from 'react-bootstrap'
import { Link } from "react-router-dom"
import ButtonGroup from 'react-bootstrap/ButtonGroup'
import AppContext from './context.js'


function LeftContainer(props) {

    const state = React.useContext(AppContext)
    const [min, setMin] = React.useState(0)
    const [max, setMax] = React.useState(0)

    const categories = {}
    const currencies = {}
    let total = 0

    for (const p of state.campaigns) {
        if (p.category in categories) {
            categories[p.category] = categories[p.category] + 1
            total += 1
        }
        else {
            categories[p.category] = 1
            total += 1
        }
    }

    for (const p of state.campaigns) {
        if (p.currencycode in currencies) {
            currencies[p.currencycode] = currencies[p.currencycode] + 1
        }
        else {
            currencies[p.currencycode] = 1
        }
    }

    return (
        <bs.Nav className="flex-column">
            <div>
                <bs.Dropdown as={ButtonGroup}>
                    <bs.Button variant="primary">
                        <Link 
                            to="/covid-home/"
                            style={{
                                color: "black"
                            }}
                        >
                            All Categories ({total})
                        </Link>
                    </bs.Button>
                    <bs.Dropdown.Toggle split variant="success" id="dropdown-split-basic" />
                    <bs.Dropdown.Menu>
                        {Object.keys(categories).map((name, idx) => {
                            return <bs.Dropdown.Item
                                key={idx}
                                className="nav-link"
                            >
                                <Link
                                    to={"/campaign-category/" + name}
                                    className="nav-link"
                                >
                                    <i>{name} ({categories[name]})</i>
                                </Link>
                            </bs.Dropdown.Item>
                        })}
                    </bs.Dropdown.Menu>
                </bs.Dropdown>
            </div>
            <hr></hr>
            <div>
                <h5>Goal Range</h5>
                <input onChange={event =>
                    setMin(event.target.value)}
                    placeholder="Min"
                    style={{
                        width: '45%'
                    }}
                />
                -
                <input onChange={event =>
                    setMax(event.target.value)}
                    placeholder="Max"
                    style={{
                        width: '45%'
                    }}
                />
                <Link
                    to={"/campaign-goal/minGoal/" + min + "/maxGoal/" + max}
                    className="nav-link btn btn-primary"
                >
                    Apply Range
                </Link>
            </div>
            <hr></hr>
            <div>
                <bs.DropdownButton id="dropdown-basic-button" title="Currency">
                    {Object.keys(currencies).map((name, idx) => {
                        return <bs.Dropdown.Item
                            key={idx}
                            className="nav-link"
                        >
                            <Link
                                to={"/campaign-currency/" + name}
                                className="nav-link"
                            >
                                <i>{name} ({currencies[name]})</i>
                            </Link>
                        </bs.Dropdown.Item>
                    })}
                </bs.DropdownButton>
            </div>
            <hr></hr>
            <div>
                <h5>Valid Charity</h5>
                <Link
                    to={"/campaign-charity/1"}
                    className="nav-link btn btn-primary"
                    style={{
                        width: '45%'
                    }}
                >
                    See only valid charities
                </Link>
                <Link
                    to={"/campaign-charity/0"}
                    className="nav-link btn btn-primary"
                    style={{
                        width: '45%'
                    }}
                >
                    See only non-valid charities
                </Link>
            </div>


        </bs.Nav>
    )
}


export default LeftContainer